import React from 'react';
import Box from '@mui/material/Box';
//import Images from './images';
import Button from '@mui/joy/Button';
import { styled } from '@mui/system';
import themes from '../Burger/theme';


const Home = () => {
  const CustomButton = styled(Button)({
    backgroundColor: themes.dark.primaryColor,
    borderColor: themes.dark.primaryColor,
    '&:hover': {
      borderColor: '#754de8',
      backgroundColor: '#000000',
    },
  });

  const CustomOutlinedButton = styled(Button)({
    borderColor: themes.dark.primaryColor,
    color: themes.dark.primaryColor,
    '&:hover': {
      borderColor: themes.dark.textWhite,
      color: themes.dark.textWhite,
    },
  });

  return (
    <Box
      component="main"
      sx={{
        flexGrow: 1,
        p: 3,
        height: '100vh',
        overflow: 'auto',
        backgroundColor: themes.dark.backgroundColor,
        minWidth: '100%'
      }}
    >
      <div>
          {/* <Images></Images> */}
          <h1>Home page</h1> <br></br>
          <h2>Lorem ipsum dolor sit amet consectetur adipisicing elit. Nobis nihil ipsam reiciendis ad nisi nostrum et, aperiam qui consequuntur deleniti adipisci, vitae illo iste, facere optio aliquid sint tenetur molestias?</h2> <br></br>
          <h1>Papagallo perche papagallo</h1><br></br>
          <CustomButton size="lg" variant="solid" sx={{marginRight: '20px'}}>Large</CustomButton>
          <CustomOutlinedButton size="lg" variant="outlined">Large</CustomOutlinedButton>
          </div>
    </Box>
  );
};

export default Home;